<template>
  <div class="natural-speech-date-picker">
    <div class="date-picker-form">
      <div class="flex-row">
        <input
          type="text"
          class="date-picker-form-input"
          v-on:blur="onBlur"
          v-on:keypress.enter="onBlur"
          ref="dateInputField"
        />
        <button
          class="date-picker-form-button"
          :class="{ active: speechActive, talking: talking }"
          v-on:click="toggleSpeech"
          :aria-label="[speechActive ? 'speech recognition off' : 'speech recognition on']"
        >
          <svgicon name="speech" width="24" height="24" color="#fff"></svgicon>
          <!--span v-if="speechActive">Off</span>
          <span v-else>On</span-->
        </button>
      </div>
      <p class="date-picker-form-output" aria-live="polite">{{ dateOutput }}</p>
      <div class="fancy">
        <span class="fancy-day" v-if="dayToStr != ''">{{ dayToStr }}</span>
        <div class="flex-col">
          <span class="fancy-date" v-if="dateToStr != ''">{{ dateToStr }}</span>
          <span class="fancy-month" v-if="monthToStr != ''">{{ monthToStr }}</span>
          <span class="fancy-year" v-if="yearToStr != ''">{{ yearToStr }}</span>
        </div>
        <span class="fancy-time" v-if="timeToStr != ''">{{ timeToStr }}</span>
      </div>
    </div>
  </div>
</template>

<script>
// TODO's:
// feedback als resultaat
// icon
// languages
// Advies
// verbeteringen, uitbreidingen parser
// koppelen kalender

let SpeechRecognition =
  window.SpeechRecognition || window.webkitSpeechRecognition;
var options = {
  weekday: "long",
  year: "numeric",
  month: "long",
  day: "numeric",
  hour: "2-digit",
  minute: "2-digit",
  second: "2-digit",
  hour12: false
};

import { Chrono } from "chrono-node";
import "../assets/icons/speech";

export default {
  name: "NlDatepicker",
  props: {
    msg: String
  },
  data() {
    return {
      chrono: new Chrono(),
      speechActive: false,
      dateOutput: "",
      now: new Date(),
      recognition: SpeechRecognition ? new SpeechRecognition() : false,
      locale: "en-gb",
      talking: false,
      dayToStr: "",
      dateToStr: "",
      monthToStr: "",
      yeartoStr: "",
      timeToStr: ""
    };
  },
  async mounted() {
    let dt = this.chrono.parseDate("today");
    this.dateOutput = " ";
    dt.toLocaleTimeString(this.locale, options);
    this.splitDate(dt);

    this.recognition.continuous = true;
    this.recognition.interimResults = false;

    this.recognition.onerror = function(event) {
      console.error(event);
    };

    this.recognition.onstart = function() {
      console.log("Speech recognition service has started");
    };

    this.recognition.onend = function() {
      this.speechActive = false;
      this.talking = false;
      console.log("Speech recognition service disconnected");
    };

    this.recognition.onaudioend = function() {
      this.speechActive = false;
      this.talking = false;
      console.log("Audio capturing ended");
    };

    this.recognition.onresult = event => {
      // this.recognition.lang = "en-US";

      let interim_transcript = "";
      let final_transcript = "";

      for (var i = event.resultIndex; i < event.results.length; ++i) {
        // Verify if the recognized text is the last with the isFinal property
        if (event.results[i].isFinal) {
          final_transcript += event.results[i][0].transcript;
        } else {
          interim_transcript += event.results[i][0].transcript;
        }
      }

      console.log("Interim: ", interim_transcript);
      console.log("Final: ", final_transcript);
      //console.log("Simple: ", event.results[0][0].transcript);

      if (final_transcript.indexOf("stop") >= 0) {
        this.recognition.stop();
        this.dateOutput =
          "I'm no longer listening. Press the button if you want use your voice.";
        return;
      }

      let dt = this.chrono.parseDate(final_transcript, this.now, {
        forwardDate: true
      });

      if (dt) {
        this.dateOutput = " ";
        dt.toLocaleTimeString(this.locale, options);
        this.splitDate(dt);
      } else
        this.dateOutput =
          "Sorry, I did not understand. Could you please try again?";
    };

    this.recognition.onspeechstart = () => {
      console.log("Speech has started");
      this.talking = true;
    };

    this.recognition.onspeechend = () => {
      console.log("Speech has stopped being detected");
      this.speechActive = false;
      this.talking = false;
    };
  },
  methods: {
    toggleSpeech() {
      this.speechActive = !this.speechActive;
      if (this.speechActive) {
        console.log("Ready to speak");
        this.recognition.start();
      } else {
        console.log("NOT ready to speak");
        this.recognition.stop();
      }
    },
    onBlur(e) {
      const element = e.target;
      let dt = "";
      if (element) {
        dt = this.chrono.parseDate(element.value, this.now, {
          forwardDate: true
        });

        if (dt) {
          this.dateOutput = " ";
          dt.toLocaleTimeString(this.locale, options);
          this.splitDate(dt);
        } else
          this.dateOutput =
            "Sorry, I did not understand that. Could you please try again?";
      }
    },
    splitDate(date) {
      const str = date.toLocaleTimeString(this.locale, options);
      const parts = str.split(",");
      const dateStr = parts[1].split(" ");
      this.dayToStr = parts[0].trim();
      this.dateToStr = dateStr[1].trim();
      this.monthToStr = dateStr[2].trim();
      this.yearToStr = dateStr[3].trim();
      this.timeToStr = parts[2].trim();
    }
  }
};
</script>

